.progress-bar__wrapped {
  transform: translateY(-50%);
}

.progress-bar__slider,
.volume-bar__slider {
  -webkit-appearance: none;
}

.progress-bar__slider::-webkit-slider-thumb,
.volume-bar__slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 0.75rem;
  height: 0.75rem;
  border-radius: 50%;
  background: rgba(244, 114, 182, 1);
}

.progress-bar__slider::-moz-range-thumb,
.volume-bar__slider::-moz-range-thumb {
  width: 0.75rem;
  height: 0.75rem;
  border-radius: 50%;
  background: rgba(244, 114, 182, 1);
}

@media (min-width: 640px) {
  .progress-bar__wrapped {
    transform: translateY(50%);
  }
}
