$gray-700: rgba(63, 63, 70, 1);
$gray-400: rgba(82, 82, 91, 1);

.comments--wrapped {
  width: 100vw;
  height: 100vh;
  padding: 10px;

  display: flex;
  flex-direction: column;

  position: absolute;
  top: 0;
  left: 0;
  transform: translate(0, 0);

  background: #fff8f8;
  border-radius: 0;

  z-index: 60;
}

.comments__info--wrapped {
  flex: none;
  padding-bottom: 10px;
  display: flex;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);

  &__title {
    flex: 1;

    & > h1 {
      font-size: 1rem;
      color: $gray-700;
      font-weight: 700;
    }

    & > p {
      font-size: 0.75rem;
      color: $gray-400;
    }
  }

  &--close {
    display: flex;
    cursor: pointer;

    &__icon {
      color: rgba(63, 63, 70, 1);
    }
  }
}

.comments__item--wrapped {
  flex: 1;
  overflow: auto;
  display: flex;
  flex-direction: column;

  &--undefined {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    &__icon {
      font-size: 10rem;
      color: $gray-400;
    }

    & > h1 {
      font-weight: 700;
      font-size: 1rem;
      color: $gray-700;
    }

    & > p {
      font-size: 0.75rem;
      color: $gray-400;
    }
  }
}

.comments__content--wrapped {
  flex: none;
  display: flex;
  align-items: center;

  &__content {
    flex: 1;
    margin-right: 0.75rem;
    padding: 6px 16px;
    background: rgba(0, 0, 0, 0.05);
    border-radius: 9999px;
    outline: none;
  }

  &__button {
    flex: none;
    font-size: 1.25rem;
    color: $gray-400;

    cursor: pointer;
  }
}

@media (min-width: 640px) {
  .comments--wrapped {
    width: 650px;
    height: 80vh;
    padding: 20px;

    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    border-radius: 10px;
  }
}
