.new-release__bg {
  background: url("../../assets/images/new-release.jpg") top/cover no-repeat;
  filter: grayscale(1);
}

.new-release__bg::after,
.new-release__screen::after {
  pointer-events: none;
  content: "";
  height: 20%;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 1;
  background: linear-gradient(to top, #f9dbdb, transparent);
}
