.slide-item {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 5px;
  overflow: hidden;
  transition: 0.9s;

  cursor: pointer;
}

.first {
  width: 15rem;
  height: 15rem;
  left: 40px;
  z-index: 3;
}

.second {
  width: 13rem;
  height: 13rem;
  left: 20px;
  opacity: 0.7;
  z-index: 2;
}

.third {
  width: 11rem;
  height: 11rem;
  left: 0;
  opacity: 0.3;
  z-index: 1;
}

.playlist__new {
  width: 188px;
  height: 233px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #fff;
  cursor: pointer;
  background: linear-gradient(33deg, #5a1eae -7%, #ce267a 117%);
  border-radius: 4px;
}

.playlist--play {
  margin-left: auto;
  padding: 6px 19px;
  font-size: 12px;
  font-weight: 700;
  color: white;
  background-color: #ea7aa0;
  border-radius: 9999px;
  transition: 0.2s;
}

.playlist--play:hover {
  opacity: 0.8;
}
