.hub--wrapped,
.hub__img--wrapped,
.hub__img {
  width: 100%;
}

.hub--wrapped {
  padding: 4.5rem 0rem 3.5rem;
}

.hub__img--wrapped {
  overflow: hidden;
}

@media (min-width: 640px) {
  .hub--wrapped {
    padding: 4.5rem 2rem 5rem;
  }

  .hub__img--wrapped {
    border-radius: 10px;
    overflow: hidden;
  }
}
