.top-1 {
  -webkit-text-stroke: 1.5px rgba(220, 38, 38, 1);
}

.top-2 {
  -webkit-text-stroke: 1.5px rgba(217, 119, 6, 1);
}

.top-3 {
  -webkit-text-stroke: 1.5px rgba(5, 150, 105, 1);
}

.top-else {
  -webkit-text-stroke: 1.5px gray;
}

.rank__selected {
  --tw-bg-opacity: 0.5;
}

.top-rank {
  font-size: 32px;
  font-weight: 900;
  text-align: center;
  color: rgba(74, 144, 226, 0);
  font-family: "Roboto", sans-serif;
  white-space: nowrap;
}

.form {
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  max-height: 100vh;
}

@media (min-width: 26rem) {
  .form {
    transform: translate(-50%, -50%);
    width: 26rem;
    height: auto;
  }
}
