.hub-list__item {
  padding: 0 1rem;
  margin-bottom: 1.3rem;
}

.hub-list__item__image--wrapped {
  position: relative;
  border-radius: 10px;
  overflow: hidden;
  cursor: pointer;
}

.hub-list__item__image--wrapped::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;

  background: rgba(0, 0, 0, 0.2);
}

.hub-list__item__image--wrapped:hover > .hub-list__item__image {
  transform: scale(1.1);
}

.hub-list__item__image {
  width: 100%;
  transition: 0.7s;
}

.hub-list__item-info {
  width: 100%;
  height: 100%;
  padding: 0 0 15px 15px;

  position: absolute;
  top: 0;
  left: 0;

  display: flex;
  flex-direction: column;
  justify-content: flex-end;

  z-index: 1;
}

.hub-list__item-info .title {
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  white-space: normal;
  line-height: 1.39;
  color: white;
  text-transform: uppercase;

  font-size: 1rem;
  margin-bottom: 0.7rem;
  font-weight: 700;
}

.hub-list__item-info .thumb-images {
  width: 100%;
  display: flex;
}

.hub-list__item-info .thumb-images .image {
  width: 20%;
  margin-right: 0.3rem;
  border-radius: 5px;
}

.hub-list__item-info .thumb-images .image:after {
  content: "";
  display: block;
  padding-bottom: 100%;
}
