.list-item > p,
.week-chart span > div > div {
  display: -webkit-box;
  -webkit-box-orient: vertical;
}

.list-item > p:nth-child(2) {
  -webkit-line-clamp: 2;
}

.list-item > p:nth-child(3),
.week-chart span > div > div {
  -webkit-line-clamp: 1;
}

.list-item > div > div {
  display: none;
  background: rgba(0, 0, 0, 0.3);
}

.list-item > div:hover {
  background: rgba(0, 0, 0, 1);
}

.list-item > div:hover > img,
.rank:hover .top-song__thumbnail {
  transform: scale(1.1);
}

.list-item > div:hover > div,
.rank:hover .top-song__screen {
  display: flex;
}