$gray-400: rgba(82, 82, 91, 1);

.comment--wrapped {
  display: flex;
  margin: 10px 0;

  &--right {
    flex-direction: row-reverse;
  }

  &__avatar {
    flex: none;
    width: 50px;
    height: 50px;
    border-radius: 9999px;
  }

  &__info {
    padding: 7px 12px 8px;
    max-width: 70%;
    height: max-content;
    border-radius: 18px;
    position: relative;

    &--right {
      margin-right: 10px;
      background: rgba(249, 198, 197, 1);
    }

    &--left {
      margin-left: 10px;
      background: rgba(0, 0, 0, 0.1);
    }

    &__author {
      position: absolute;
      bottom: 0;

      white-space: nowrap;

      font-size: 14px;
      color: $gray-400;

      transform: translateY(100%);

      &--right {
        right: 0;
      }

      &--left {
        left: 0;
      }
    }

    &__content {
      word-break: break-word;
    }
  }
}
