.news-item--wrapped {
  position: relative;
}

.news-item {
  position: absolute;
  top: 0;
  transition: all 0.7s;
  transform-style: preserve-3d;
  perspective: 1000px;
}

.news-item--last,
.news-item--selected {
  left: 50%;
}

.news-item--selected {
  transform: translateX(-50%);
}

.news-item--last {
  opacity: 0;
  transform: translateX(-50%) scale(0.85);
}

.news-item--previos {
  left: 0;
  transform: scale(0.85);
}

.news-item--next {
  left: 100%;
  transform: translateX(-100%) scale(0.85);
}

.news-item--previos,
.news-item--next {
  z-index: 2;
}

.news-item--selected {
  z-index: 3;
}

.news-item > img {
  width: 100%;
}

.news-item--sub {
  opacity: 0;
}

.news-item__dot--wrapped {
  display: flex;
  margin-top: 1rem;
}

.news-item__dot {
  flex: none;
  width: 6px;
  height: 6px;
  margin: 0 3px;
  background: #d8d8d8;
  border-radius: 9999px;
  transition: all 0.5s ease-out;
  will-change: width;
  cursor: pointer;
}

.news-item__dot--selected {
  width: 24px !important;
  background: linear-gradient(104deg, #3023ae, #c86dd7 102%) !important;
}
